'use strict';

var React = require('react');
var Reflux = require('reflux');

var DateFilter = React.createClass({displayName: "DateFilter",

    onStatusChange: function(status, selection) {
        console.log('onStatusChange -> '+status);
        debugger;
        this._setStartDate(status.sd);
        this._setEndDate(status.ed);
    },

    _setStartDate: function(date){
        $(this.getDOMNode()).find('.start-date').datepicker('setDate', date);
        //this.actions.updateItemValue('sd', date);
        this.setState({'startDate': date}); 
    },    

    _setEndDate: function(date){
        $(this.getDOMNode()).find('.end-date').datepicker('setDate', date);
        //this.actions.updateItemValue('ed', date);  
        this.setState({'endDate': date});
    },

    _clearStartDate: function(date){
        this._setStartDate('');
    },    

    _clearEndDate: function(date){
        this._setEndDate('');   
    },

    componentDidUpdate: function(){
        var self = this;
        $('.start-date').datepicker({
            dateFormat: "dd/mm/yy",
            changeMonth: true,
            changeYear: true,
            onSelect: function(dateText) {
                self.actions.updateItemValue('sd', dateText);
            }
        });
        $('.end-date').datepicker({
            dateFormat: "dd/mm/yy",
            changeMonth: true,
            changeYear: true,
            onSelect: function(dateText) {
                self.actions.updateItemValue('ed', dateText);
            }
        });      
    },    

    componentDidMount: function(){
        this.actions = this.props.actions;
        this.unsubscribe = this.props.store.listen(this.onStatusChange);        
    },   

    componentWillUnmount: function() {
        this.unsubscribe();
    },

    getInitialState: function() {
        return {
            startDate: '',
            endDate: ''
        };
    },

    render: function() {  
        console.log('DateFilter -> render');
        if (this.props.active){
            return(
                React.createElement("div", {className: "tab-content"}, 
                    React.createElement("div", {className: "tab-pane fade active in"}, 
                        React.createElement("div", {className: "filter-group-panel selected"}, 
                            React.createElement("div", {className: "filter-group-panel-header"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.date"}))
                            ), 
                            React.createElement("div", {className: "input-group date"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.startDate"})), 
                                React.createElement("input", {type: "text", className: "start-date"}), 
                                React.createElement("i", {className: "fa fa-trash", onClick: this._clearStartDate})
                            ), 
                            React.createElement("div", {className: "input-group date"}, 
                                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: "filters.endDate"})), 
                                React.createElement("input", {type: "text", className: "end-date"}), 
                                React.createElement("i", {className: "fa fa-trash", onClick: this._clearEndDate})
                            )
                        )
                    )
                )
            );  
        } else {
            return null;
        }
    }
});

module.exports = DateFilter;